.list-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 10px;
    width: 90%;
    height: auto;
    padding-top: 28px;
    max-height: 300px;
    overflow-y: auto;
    padding-left: 0;
}

.list-container::-webkit-scrollbar {
    display: none;
}